import React from 'react'
import { Carousel } from 'react-bootstrap';


function Carrusel() {
    return (
            <Carousel fade>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src="https://hdwallpaperim.com/wp-content/uploads/2017/08/22/379520-space-planet.jpg"
                    alt="First slide"
                    />
                    <Carousel.Caption>
                    <h3>Planet 1</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src="https://trancentral.tv/wp-content/uploads/2018/10/R4cFkKN-space-planets-wallpaper.jpg"
                    alt="Second slide"
                    />

                    <Carousel.Caption>
                    <h3>Planet 2</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src= "https://s1.1zoom.me/big0/118/429618-Kycb.jpg"
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                    <h3>Planet 3</h3>
                    <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel> 
    )
}

export default Carrusel
