import React from 'react'
import {Form, Button} from 'react-bootstrap'


function Contactus() {
    return (
        <>
            <div className = "fondo1">
                <div className = "contact">
                    <div className ="contact-form">
                        <Form>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Nombre</Form.Label>
                                <Form.Control type="text" placeholder="David Felipe" />
                                <Form.Label>Apellido</Form.Label>
                                <Form.Control type="text" placeholder="Castro Herrera" />
                                <Form.Label>Correo</Form.Label>
                                <Form.Control type="email" placeholder="correoejemplo@gmail.com" />
                                <Form.Text className="text-muted">
                                Nunca compartiremos su informacion con nadie mas.
                                </Form.Text>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formBasicPassword">        
                                <Form.Label>Su comentario</Form.Label>
                                <Form.Control as="textarea" rows={3} />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                <Form.Check type="checkbox" label="Pruebame" />
                            </Form.Group>
                            <Button variant="primary" type="submit">
                                Enviar
                            </Button>
                        </Form>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Contactus
